export default {
	created() {
		var neirong = this.questionList[this.number-1]
		if(this.tag == "l"){
			neirong = this.questionList[this.qNumber]
		}
		this.content = neirong.content
		this.jiexi = neirong.jiexi
		this.rightAnswer = neirong.rightchoices
		var a = `A. ${neirong.A}`
		var b = `B. ${neirong.B}`
		var c = `C. ${neirong.C}`
		var d = `D. ${neirong.D}`
		this.choices.push(a)
		this.choices.push(b)
		this.choices.push(c)
		this.choices.push(d)
	},
	data() {
		return {
			content: '',
			choices: [],
			isActive: [],
			answer: [],
			jiexi: '',
			rightAnswer: []
		}	
	},
	methods: {
		activeChoice (e){
			var arryindex = this.isActive.indexOf(e);
			if(arryindex == -1){
				this.isActive.push(e)
				if(e == 0){
					this.answer.push("A")
				}else if(e == 1){
					this.answer.push("B")
				}else if(e == 2){
					this.answer.push("C")
				}else if(e == 3){
					this.answer.push("D")
				}
			}else{
				this.isActive.splice(arryindex,1)
				this.answer.splice(arryindex,1)
			}
		},
		changeQuestion(e){
			var data ={}
			if(this.tag == "l"){
				data = {
					ps: e
				}
			}else {
				if(this.nextText == "提交"){
					data = {
						ps: e,
						number: this.number-1,
						answer: this.answer,
						submit: true,
						tixing: this.questionList[this.number-1].tixing
					}
				}else{
					data = {
						ps: e,
						number: this.number-1,
						answer: this.answer,
						submit: false,
						tixing: this.questionList[this.number-1].tixing
					}
				}
			}	
			this.$emit('func', data);
		}
	},
	props:[
		"number",
		"questionList",
		"nextText",
		"jiexiTag",
		"qNumber",
		"tag"
	]
}
